import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../../shared/shared.module';
import { ReactiveFormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
import { SwiperModule } from 'ngx-swiper-wrapper';
import { InputFileModule } from 'ngx-input-file';
import { ProductListComponent } from './product-list/product-list.component';
import { ProductDetailComponent } from './product-detail/product-detail.component';
import { ProductZoomComponent } from './product-detail/product-zoom/product-zoom.component';
import { AddProductComponent } from './add-product/add-product.component';
import { CategoriesComponent } from './categories/categories.component';
import { CategoryDialogComponent } from './categories/category-dialog/category-dialog.component';
import { SubcategoriesComponent } from './subcategories/subcategories.component';
import { MaterialComponent } from './material/material.component';
import { ProductTypeComponent } from './product-type/product-type.component';
import { MaterialDialogComponent } from './material/material-dialog/material-dialog.component';
import { ProducttypedialogComponent } from './product-type/producttypedialog/producttypedialog.component';
import { ProductColorComponent } from './product-color/product-color.component';
import { ColorDialogComponent } from './product-color/color-dialog/color-dialog.component';
import { SubcatDialogComponent } from './subcategories/subcat-dialog/subcat-dialog.component';
import { NgxImageZoomModule } from 'ngx-image-zoom';

export const routes = [ 
  { path: '', redirectTo: 'product-list', pathMatch: 'full'},
  { path: 'categories', component: CategoriesComponent, data: { breadcrumb: 'Categories' } },
  { path: 'subcategories', component: SubcategoriesComponent, data: { breadcrumb: 'SubCategories' } },
  { path: 'producttype', component: ProductTypeComponent, data: { breadcrumb: 'Product Type' } },
  { path: 'material', component: MaterialComponent, data: { breadcrumb: 'Material' } },
  { path: 'color', component: ProductColorComponent, data: { breadcrumb: 'Color' } },
  { path: 'product-list', component: ProductListComponent, data: { breadcrumb: 'Product List' } },
  { path: 'product-detail', component: ProductDetailComponent, data: { breadcrumb: 'Product Detail' } },
  { path: 'product-detail/:id', component: ProductDetailComponent, data: { breadcrumb: 'Product Detail' } }, 
  { path: 'add-product', component: AddProductComponent, data: { breadcrumb: 'Add Product' } },
  { path: 'add-product/:id', component: AddProductComponent, data: { breadcrumb: 'Edit Product' } }, 
];

@NgModule({
  declarations: [
    ProductListComponent,
    ProductDetailComponent,
    ProductZoomComponent,
    AddProductComponent,
    CategoriesComponent,
    CategoryDialogComponent,
    SubcategoriesComponent,
    MaterialComponent,
    ProductTypeComponent,
    MaterialDialogComponent,
    ProducttypedialogComponent,
    ProductColorComponent,
    ColorDialogComponent,
    SubcatDialogComponent
  ],
  imports: [
    NgxImageZoomModule,
    CommonModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    SharedModule,
    NgxPaginationModule,
    SwiperModule,
    InputFileModule
  ]
})
export class ProductsModule { }
